var ButtonGroup = React.createClass({
    getInitialState: function() {
        return {
        };
    },

    getDefaultProps: function() {
        return {
            label : 'Action',
            options : [
                {
                label : 'Action1',
                handleClick : null,
                divider : false
                },
            ]
        };
    },

    render: function() {
        var options = this.props.options.map(function(value, key){
            return <ButtonGroupOption key={key} label={value.label} handleClick={value.handleClick} divider={value.divider} />;
        });

        return (
            <div className="btn-group">
                <button type="button" className="btn btn-default btn-sm dropdown-toggle"
                     data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                    {this.props.label} <span className="caret"></span>
                </button>
                <ul className="dropdown-menu">
                    {options}
                </ul>
            </div>
        );
    }
});

var ButtonGroupOption = React.createClass({
    getDefaultProps: function() {
        return {
            label : null,
            handleClick : null,
            divider : false
        };
    },

    render: function() {
        if (this.props.divider) {
            return (
                <li role="separator" className="divider"></li>
            );
        } else {
            return (
                <li onClick={this.props.handleClick}><a style={{cursor : 'pointer'}}>{this.props.label}</a></li>
            );
        }
    }

});

var WaitingListStatus = React.createClass({

    getDefaultProps: function() {
        return {
            visitor : null,
            visitNumber : 0,
            reload : null,
        };
    },

    intakeComplete : function()
    {
        if (confirm('Click OK if student completed their intake form.')) {
             $.post('counseling/Admin/Dashboard/Waiting/', {
             	command : 'intakeComplete',
                visitorId : this.props.visitor.id
             }, null, 'json')
             	.done(function(data){
                    this.props.reload();
             	}.bind(this));
        }

    },

    render: function() {
        if (this.props.visitor.intake_complete === '1') {
            if (this.props.visitNumber > 1) {
                if (this.props.visitor.seen_last_visit === '0') {
                    return <span className="label label-danger">Unseen last visit</span>;
                } else {
                    return (
                        <span className="label label-primary">
                            Previously seen @ {this.props.visitor.previously_seen}
                        </span>
                    );
                }
            } else {
                return <span className="label label-success">Intake complete</span>
            }
        } else {
            return <span className="label label-danger" style={{cursor:'pointer'}} title="Click to acknowledge intake completion" onClick={this.intakeComplete}>Intake incomplete</span>
        }
    }

});

var Modal = React.createClass({
    getInitialState: function() {
        return {header: null, body: null, footer: null};
    },

    getDefaultProps: function() {
        return {header: null, body: null, footer: null, modalId: 'reactModal', onClose: null};
    },

    componentDidMount: function() {
        if (this.props.onClose) {
            $('#' + this.props.modalId).on('hidden.bs.modal', function (e) {
                this.props.onClose();
            }.bind(this));
        }
    },

    render: function() {
        return (
            <div id={this.props.modalId} className="modal fade" tabindex="-1" role="dialog">
                <div className="modal-dialog">
                    <div className="modal-content">
                        <div className="modal-header">
                            <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                            </button>
                            <h4 className="modal-title">{this.props.header}</h4>
                        </div>
                        <div className="modal-body">
                            {this.props.body}
                        </div>
                        <div className="modal-footer">
                            {this.props.footer}
                            <button type="button" className="btn btn-default" data-dismiss="modal">Close</button>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
});

var Summary = React.createClass({
    getInitialState: function() {
        return {error: null, errorMessage: null};
    },

    getDefaultProps: function() {
        return {data: null, time : null};
    },

    serverError: function() {
        this.setState({error: true, errorMessage: 'Could not retrieve server data'});
    },

    render: function() {
        var currentTally = null;
        var totalTally = null;
        if (this.props.data) {
            currentTally = this.props.data.currentTally;
            totalTally = this.props.data.completeTally;
        } else {
            currentTally = {
                emergencies: 0,
                walkin: 0,
                appointment: 0,
                other: 0
            };
            totalTally = currentTally;
        }
        return (
            <div className="summary">
                {this.state.error
                    ? <div className="alert alert-danger">{this.state.errorMessage}</div>
                    : null}
                <div className="row">
                    <div className="col-sm-6 left">
                        <h3>Current</h3>
                        <div className="row">
                            <div className="col-sm-4">
                                <SummaryTotalWaiting {...this.props.data}/>
                            </div>
                            <div className="col-sm-4">
                                <SummaryEstimatedWait {...this.props.data}/>
                            </div>
                            <div className="col-sm-4">
                                <SummaryWaitingTally {...currentTally}/>
                            </div>
                        </div>
                    </div>
                    <div className="col-sm-6 right">
                        <h4 className="pull-right">{this.props.time}</h4><h3>Today</h3>
                        <div className="row">
                            <div className="col-sm-2">
                                <SummaryTotalSeen {...this.props.data}/>
                            </div>
                            <div className="col-sm-3">
                                <SummaryCompleted {...this.props.data}/>
                            </div>
                            <div className="col-sm-3">
                                <SummaryAverageWait {...this.props.data}/>
                            </div>
                            <div className="col-sm-4">
                                <SummaryWaitingTally {...totalTally} showLabels={true}/>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }

});


var SummaryCompleted = React.createClass({
    getInitialState: function() {
        return {
            tooltip : true
        };
    },

    getDefaultProps: function() {
        return {totalComplete: 0, leaveReasons : null};
    },

    componentDidMount : function() {
        $(window).load(function(){
            this.loadTooltip();
        }.bind(this));
    },

    loadTooltip : function() {
        $('.total-complete div.big-number').popover({
            animation:true,
            placement:'bottom',
            trigger:'hover',
            container :'.summary .right',
            title : 'Not seen reasons',
            html : true
        });
        $('.total-complete div.big-number').css('cursor', 'pointer');
        this.setState({tooltip : false});
    },

    render: function() {
        var reasons = null;
        if (this.props.leaveReasons !== null) {
            reasons = this.props.leaveReasons.join('<br />');
        }
        return (
            <div className="total-complete text-center">
                <div className="big-number" data-content={reasons}>
                    {this.props.totalComplete}
                </div>
                <div>Total<br />Visits</div>
            </div>
        );
    }

});

