var WaitingAction = React.createClass({

    getDefaultProps: function() {
        return {
            visitId : 0
        };
    },

    completeReason : function(reason) {
        $.post('counseling/Admin/Dashboard/Waiting', {
        	command : 'setCompleteReason',
            reason : reason,
            visitId : this.props.visitId
        }, null, 'json')
        	.done(function(data){
                this.props.reload();
        	}.bind(this));
    },

    remove : function() {
        if (confirm('Are you sure you want to remove this visitor?')) {
            $.post('counseling/Admin/Dashboard/Waiting', {
            	command : 'delete',
                visitId : this.props.visitId
            }, null, 'json')
            	.done(function(data){
                    this.props.reload();
            	}.bind(this));
        }
    },

    getOptions : function() {
        var options = [];
        options.push(
            {
                label : <div><i className="fa fa-external-link"></i> Had to leave</div>,
                visitId : this.props.visitId,
                handleClick : this.completeReason.bind(null, 2)
            },
            {
                label : <div><i className="fa fa-eye-slash"></i> Missing</div>,
                visitId : this.props.visitId,
                handleClick : this.completeReason.bind(null, 3)
            },
            {
                label : <div><i className="fa fa-clock-o"></i> Made appointment</div>,
                visitId : this.props.visitId,
                handleClick : this.completeReason.bind(null, 4)
            },
            {
                divider : true
            },
            {
                label : <div className="text-danger"><i className="fa fa-trash-o"></i> Remove</div>,
                visitId : this.props.visitId,
                handleClick : this.remove
            }
        );
        return options;
    },

    render: function() {
        var options = this.getOptions();
        return (
            <ButtonGroup options={options}/>
        );
    }

});

var refreshDashboard = null;

var Dashboard = React.createClass({
    getInitialState: function() {
        return {
            refresh : true,
            emergencyList : null,
            waitingList : null,
            summary : null,
            currentlySeen: null,
            time : null
        };
    },

    componentDidMount : function() {
        this.loadData();
    },

    refresh : function() {
        if (this.state.refresh) {
            refreshDashboard = setInterval(function(){
                this.loadData();
            }.bind(this), 30000);
        }
    },

    loadData : function() {
        clearInterval(refreshDashboard);
        $.getJSON('counseling/Admin/Dashboard/Waiting', {
        	command : 'list'
        }).done(function(data){
            this.setState({
                emergencyList : data.emergencies,
                waitingList : data.waiting,
                summary : data.summary,
                currentlySeen: data.currentlySeen,
                time : data.time
            });
            this.refresh();
        }.bind(this));
    },

    render: function() {
        return (
            <div className="dashboard">
                <Summary data={this.state.summary} time={this.state.time} reload={this.loadData}/>
                <CurrentlySeen seen={this.state.currentlySeen} reload={this.loadData}/>
                <Waiting emergency={this.state.emergencyList} waiting={this.state.waitingList} reload={this.loadData}/>
            </div>
        );
    }

});

ReactDOM.render(<Dashboard/>, document.getElementById('dashboard'));
